import {_decorator, Component, Node, profiler, MeshRenderer, Mesh} from 'cc';
const {ccclass, property} = _decorator;

class Size {
  values: number[];
  public constructor(width: number, height: number) {
    this.values = [width, height];
  }
  get x(): number {
    return this.values[0];
  }
  get y(): number {
    return this.values[1];
  }
}

class Game_Board {
  grid_size: Size = new Size(8, 8);
	mesh: MeshRenderer;
	public render() {
		let mat = this.mesh.getMaterial;
	}
}

